<template>
  <div>
    <h1>webscoket</h1>
    <el-row>
      <el-button @click="initWebSocket()"></el-button>
      <el-button type="primary" @click="disconnect()"></el-button>
      <el-button type="success" @click="send()"></el-button>
      <el-button type="success" @click="sendTopic()"></el-button>
      <el-button type="success" @click="sendAllUser()"></el-button>
      <el-button type="success" @click="sendTopic()"></el-button>
      <el-input v-model="input"></el-input>
    </el-row>
    <el-row>
      <el-input type="textarea" v-model="message"></el-input>
    </el-row>

  </div>
</template>
<script>
// 
import SockJS from 'sockjs-client';
import Stomp from 'stompjs';
import {
  getToken
} from '@/utils/authentication'
export default {
  data () {
    return {
      input: '',
      message: '',
      dataList: []
    };
  },
  mounted: function () {

  },
  beforeDestroy: function () {
    // ,
    this.disconnect();
    clearInterval(this.timer);
  },
  methods: {
    initWebSocket () {
      this.connection();
      let self = this;
      // ,,
      this.timer = setInterval(() => {
        try {
          self.stompClient.send("test");
        } catch (err) {
          console.log(": " + err);
          self.connection();
        }
      }, 5000);
    },
    removeTab (targetName) {
      console.log(targetName)
    },
    connection () {
      // 
      this.socket = new SockJS('http://127.0.0.1:8081/myUrl?token=' + getToken());//
      // STOMP
      this.stompClient = Stomp.over(this.socket);
      // ,
      var headers = {
        login: 'mylogin',
        passcode: 'mypasscode',
        // additional header
        'client-id': 'my-client-id'
      };
      // websocket
      this.stompClient.connect(headers, () => {
        this.stompClient.subscribe('/user/queue/sendUser', (msg) => { // topic
          console.log(msg.body);  // msg.body
        });
        this.stompClient.subscribe('/topic/sendTopic', (msg) => { //
          console.log(msg)
        });
        console.log("");
      }, (err) => {
        // 
        console.log("");
        console.log(err);
      });
    },
    // 
    disconnect () {
      if (this.stompClient != null) {
        this.stompClient.disconnect();
        console.log("Disconnected");
      }
    }
    , send () {
      // this.$api.webscoket.send();
      this.stompClient.send("/sendUser", {}, JSON.stringify({ name: 'SuperAdmin' }))

    },
    sendTopic () {
      // this.$api.webscoket.sendTopic();
      this.stompClient.send("/sendTopic")
    },
    sendAllUser () {
      this.$api.webscoket.sendAllUser({ message: "" });
    },
  }
};
</script>